<template>
  <div class="components-container">
    <div class="editor-container">
      <UE :defaultMsg="defaultMsg" :config="config" :id="ue1" ref="ue"></UE>
    </div>
     <button @click="getUEContent()"></button>
    <button @click="getUEContentTxt()"></button>
  </div>
</template>
<style>
.info {
  border-radius: 10px;
  line-height: 20px;
  padding: 10px;
  margin: 10px;
  background-color: #ffffff;
}
</style>
<script>
import UE from "../components/edmitor";
export default {
  components: { UE },
  data() {
    return {
      defaultMsg:
        '<span style="orphans: 2; widows: 2; font-size: 22px; font-family: KaiTi_GB2312; "><strong></strong></span>',
      config: {
        initialFrameWidth: null,
        initialFrameHeight: 350
      },
      ue1: "ue1", // id
    };
  },
  mounted(){

  },
  methods: {
    getUEContent() {
      let content = this.$refs.ue.getUEContent(); // 
      this.$notify({
        title: "",
        message: content,
        type: "success"
      });
      console.log(content);
    },
    getUEContentTxt() {
      let content = this.$refs.ue.getUEContentTxt(); // 
      this.$notify({
        title: "",
        message: content,
        type: "success"
      });
      console.log(content);
    }
  }
};
</script>



